@import"https://fonts.googleapis.com/css?family=Poppins&display=swap";body{font-family:"Poppins"}.navigation{height:100px}.nav-container{max-width:1100px;margin:0 auto}.brand{position:absolute;padding-left:20px;float:left;line-height:100px;text-transform:uppercase;font-size:1.4em;transition:all .3s ease-in}.brand:hover{transform:scale(1.07)}.brand a,.brand a:visited{color:#000;text-decoration:none}nav{float:right}nav ul{list-style:none;margin:0;padding:0}nav ul button{float:left;position:relative}nav ul button p{display:block;margin-top:22px;padding:0 20px 0 20px;line-height:50px;background:#fff;color:#000;text-decoration:none;transition:all .3s ease-in}nav ul button p::before{transition:300ms;height:4px;content:"";position:absolute;background-color:#4f46e5;width:0%;bottom:0px}nav ul button p:hover{transform:scale(1.09)}nav ul button p:hover::before{width:70%}nav ul button p:not(:only-child):after{padding-left:4px;content:" ▾"}nav ul button ul li{min-width:190px}nav ul button ul li a{padding:15px;line-height:20px}.nav-dropdown{display:none;position:absolute;z-index:1;box-shadow:0 3px 12px rgba(0,0,0,.15)}.nav-mobile{display:none;position:absolute;top:0;right:0;height:100px;width:100px}@media only screen and (max-width: 896px){.nav-mobile{display:block}nav{width:100%;padding:100px 0 15px}nav ul{display:none}nav ul li{float:none}nav ul li a{padding:15px;line-height:20px}nav ul li a:hover::before{width:20%}nav ul li ul li a{padding-left:30px}.nav-dropdown{position:static}}#nav-toggle{position:absolute;left:18px;top:35px;cursor:pointer;padding:10px 35px 16px 0px}#nav-toggle span,#nav-toggle span:before,#nav-toggle span:after{cursor:pointer;border-radius:1px;height:5px;width:35px;background:#4f46e5;position:absolute;display:block;content:"";transition:all 300ms ease-in-out}#nav-toggle span:before{top:-10px}#nav-toggle span:after{bottom:-10px}@media screen and (min-width: 896px){.navigation{background-color:#fff}.nav-list{display:block !important}}